.open-note-buttons-container {
    width: 50%;
    margin: auto;
}

.tomatoes-container{
    height: 30px;
    display: flex;
    justify-content: space-between;
}

.tomato{
    border: none;
    height: 30px;
    aspect-ratio: 1;
    background-color: transparent;
    background-image: url("../../images/TOMATO.webp");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    filter: grayscale(1);
}
.activeTomato{
    filter: grayscale(0);
}

.bottomButtons{
    display: flex;
    margin-top: 10px;
}

.cancel-button{
    width: 30px;
    height: 30px;
    border-radius: 30px 0 0 30px;
    border: none;
    background-color: rgb(156, 156, 156);
    cursor: pointer;
}

#startButton{
    width: 100%;
    border: none;
    background-color: transparent;
    cursor: pointer;
}

.progressBar{
    height: 30px;
    width: 100%;
    border-radius: 0 30px 30px 0;
    position: relative;
}
.progressBar > span{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(255, 255, 255, .75);
    padding: 0 10px;
    border-radius: 30px;
}

.status{
    height: 30px;
    border: none;
}