.note {
    width: 300px;
    height: 200px;
    overflow-y: scroll;
    animation: spawn 0.3s;
}

.note-header {
    background-color: rgba(0, 0, 0, 0.1);
    color: black;
    padding: 5px 15px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.note-header > button {
    border-radius: 50%;
    width: 25px;
    aspect-ratio: 1;
    cursor: pointer;
    border: none;
}

.note-body {
    padding: 10px;
    word-break: normal;
    overflow-wrap: anywhere;
    hyphens: auto;
    cursor: pointer;
    height: 100%;
}
