*,
*::after,
*::before:not(i) {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto", sans-serif;
    font-family: "Nunito", sans-serif;
}

:root {
    --main-color: rgb(0, 81, 255);
}

::-webkit-scrollbar {
    width: 10px;
  }
  
  ::-webkit-scrollbar-track {
    background: #f1f1f1;
  }
  
  ::-webkit-scrollbar-thumb {
    background: #888;
  }
  
  /* Handle on hover */
  ::-webkit-scrollbar-thumb:hover {
    background: #555;
  }

.App {
    width: 100vw;
    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;

    padding: 50px;

    background-color: rgb(179, 134, 10);
    background-image: url("./images/bg.jpg");
    background-size: cover;
    overflow-x: hidden;
}

.logo {
    width: 300px;
    margin-bottom: 10px;
    z-index: 10;
}

.notes-container {
    width: 100%;
    height: 100%;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    place-items: center;
    gap: 30px;
}

.add-task {
    padding: 5px 30px;
    outline: none;
    border: none;
    border-radius: 30px;
    background-color: var(--main-color);
    color: white;
    font-weight: bolder;
    font-size: 1rem;
    box-shadow: 2px 2px 0 0 black;
    cursor: pointer;
    animation: spawn 0.3s;
    margin-bottom: 20px;
}

@keyframes spawn {
    from {
        opacity: 0;
        filter: blur(10px);
    }
    to {
        opacity: 1;
        filter: blur(0);
    }
}
