.open-note-container {
    box-shadow: 0 0 0 10000px rgb(255, 187, 187);
    background-color: rgb(255, 187, 187);
    animation: spawn 0.3s;
    width: 95%;
    max-width: 600px;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    height: 90%;
    word-break: normal;
    overflow-wrap: anywhere;
    hyphens: auto;
}


.open-note-body {
    height: 100%;
    overflow-y: scroll;
}


h1 {
    text-align: center;
}

p {
    font-size: 1.2rem;
    line-height: 1.6;
}

